import { Controller, Selection } from '@ali/4ever-cangjie';
import { Color } from '@ali/4ever-plugin-marks';
import { addMarkAtRange } from '@ali/4ever-utils';


export default function setColorAtRange(controller: Controller, range: Selection, color: string) {
  const mark = Color.create(color);
  return addMarkAtRange(controller, range, mark);
}
